:root {
  --main-text-color: #00acdc;
}

.body {
  margin: 0;
}

.header {
  text-transform: uppercase;
  box-sizing: border-box;
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding-inline: 50px;
  font-family: Roboto, Arial, Helvetica, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
  display: flex;
}

.logo {
  display: flex;
}

.nav__list {
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  color: #000;
  justify-content: center;
  align-items: center;
  height: 60px;
  margin-left: 20px;
  text-decoration: none;
  display: flex;
}

.is-active {
  color: var(--main-text-color);
  align-items: center;
  height: 60px;
  margin-left: auto;
  text-decoration: none;
  display: flex;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--main-text-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
}

.nav__link:hover {
  color: var(--main-text-color);
}
